@import '../variables.scss';

.ratings{
    width:  90%;
    margin: $spacing-mobile * 2 auto;

    @media (min-width: 1024px){
        width: auto;
        margin-left: 25%;
    }

    @media (min-width: 1440px){
        margin-left: 20%;
    }

    &__text{
        color: #444;
        text-align: center;
        font-size: 1.6rem;

        @media (min-width: 1024px){
            font-size: 2.2rem;
        }
    }
}