@import '../variables.scss';
@import '../mixins.scss';

.settings{
    width: 90%;
    margin: auto;

    @media (min-width: 640px){
        width: 80%;
    }

    @media (min-width: 1024px){
        display: grid;
        grid-template-columns: 1fr 1fr;
        width: auto;
        margin-left: 30%;
        margin-top: $spacing-mobile;
        grid-column-gap: 10px;
    }

    @media (min-width: 1440px){
        margin-left: 25%;
    }

    &__option{
        padding-bottom: $spacing-mobile;
        padding-top: $spacing-mobile;

        &-title{
            @include settingsTitle;
        }

        &-text{
            margin-top: $spacing-mobile;
            font-size: 1.4rem;
            line-height: 50px;
        }

        &-change{
            margin-top: $spacing-mobile;

            &-title{
                margin-top: $spacing-mobile;
                margin-bottom: $spacing-mobile;
                font-size: 1.5rem;
                color: #444;

                @media (min-width: 1024px){
                    font-size: 1.8rem;
                }
            }

            .settings__option-form{
                line-height: 50px;
                font-size: 1.4rem;

                @media (min-width: 1024px){
                   font-size: 1.6rem;
                }

                div{
                    margin-left: 5px;
                    margin-right: 5px;

                    &.special{
                        padding-right: 10%;
                    }
                }

                input{
                    font-size: 1.1rem;

                    @media (min-width: 1024px){
                        font-size: 1.3rem;
                    }
                }

                button{
                    border: none;
                }

                svg{
                    font-size: 2rem;
                    cursor: pointer;
                }
            }
        }
    }

    &__buttons{
        margin-top: $spacing-mobile;

        @media (min-width: 1024px){
            margin-top: 0;
            display: flex;
            flex-wrap: wrap;
            align-content: center;
            height: 100%;
        }

        button{
            display: block;
            margin: auto;
            width: 75%;
            @include walletBtn;
            padding: 10px;
            margin-top: $spacing-mobile;
            font-size: 1.6rem;
        }
    }
}