@import '../variables';

.user{
    width: 90%;
    margin: auto;
    min-height: 435px;
    background-color: lighten($color: #0464FC, $amount: 10%);
    border-radius: 70px;
    padding-top: $spacing-desktop;
    color: white;

    @media (min-width: 640px){
        width: 80%;
    }

    @media (min-width: 1024px){
        margin-top: $spacing-mobile * 2;
        margin-left: 30%;
        margin-right: 5%;
        min-height: 275px;
        width: auto;
    }

    @media (min-width: 1440px){
        margin-left: 25%;
        margin-right: 25%;
    }

    &__image-container{
        position: relative;
        background-color: #ddd;
        width: 120px;
        margin: auto;
        border-radius: 50%;

        @media (min-width: 1024px){
            position: absolute;
            margin-left: $spacing-desktop;
            margin-top: calc(235px / 2 - 60px);
        }

        img{
            width: 120px;
            overflow: hidden;
            border-radius: 50%;
        }

        .user__add-image{
            position: absolute;
            left: 50%;
            bottom: 0;
            transform: translate(-50%);
            width: 25px;
            height: 25px;
            font-size: 2rem;
            background-color: rgba($color: black, $alpha: .4);
            border-radius: 50%;
            border: 2px solid $special-fontcolor;
            cursor: pointer;

            &:hover{
                width: 100%;
                height: 100%;
                font-size: 4rem;
            }

            svg{
                position: absolute;
                left: 50%;
                top: 50%;
                transform: translate(-50%, -50%);
            }
        }
    }

    &__title{
        text-align: center;
        margin-top: $spacing-desktop;
        font-size: 1.7rem;

        @media (min-width: 1024px){
            margin-left: 170px;
            text-align: left;
            font-size: 2.2rem;
        }
    }

    &__info-and-option{
        @media (min-width: 1024px){
            display: flex;
        }
    }

    &__information-container{
        margin-top: $spacing-desktop;

        @media (min-width: 1024px){
            margin-left: 170px;
            flex-basis: 40%;
        }

        .user__information{
            margin-top: 7px;
            &-type{
                margin-left: $spacing-desktop;
                font-size: 1.4rem;
                font-weight: bold;

                @media (min-width: 1024px){
                    margin-left: 0;
                    font-size: 1.6rem;
                }

                @media (min-width: 1440px){
                    margin-left: 0;
                    font-size: 1.7rem;
                }

                span{
                    font-weight: normal;
                }
            }
        }
    }

    &__option{
        margin-top: $spacing-mobile;

        @media (min-width: 1024px){
            margin-top: 0;
            align-self: center;
        }
        
        button{
            display: block;
            margin: auto;
            padding: 10px 20px;
            background-color: darken($color: #0464FC, $amount: 20%);
            font-family: $title-font;
            font-weight: bold;
            color: white;
            border: 1px solid white;
            cursor: pointer;

            @media (min-width: 1024px){
                font-size: 1.5rem;
            }
        }
    }
}

.seemore{
    width: 90%;
    margin: auto;
    padding: $spacing-mobile 0;

    @media (min-width: 640px){
        width: 80%;
    }

    @media (min-width: 1024px){
        width: auto;
        margin: $spacing-mobile * 2 0 0 30%;
    }

    @media (min-width: 1440px){
        margin-left: 25%;
    }

    &__title{
        font-size: 1.6rem;
        color: #444;
        padding-bottom: $spacing-desktop;

        @media (min-width: 1024px){
            font-size: 2rem;
            padding-bottom: $spacing-mobile;
        }
    }

    &__options{
        p{
            padding-top: 10px;
            font-size: 1.3rem;
            line-height: 1.3;

            @media (min-width: 1024px){
                padding-top: $spacing-desktop;
                font-size: 1.5rem;
                line-height: 1.4;
            }

            a{
                color: #0464FC;
            }
        }
    }
}