@import '../variables';
@import '../mixins';

.wallet{
    margin: auto;
    width: 90%;

    @media (min-width: 1024px){
        display: flex;
        justify-content: space-around;
        margin-left: 25%;
        margin-top: $spacing-mobile;
        width: auto;
    }

    @media (min-width: 1440px){
        margin-left: 30%;
        margin-right: 10%;
        margin-top: $spacing-mobile * 2;
    }

    &__card{
        position: relative;
        margin: $spacing-mobile auto;
        width: 304px;
        height: 190px;
        background-image: url(../../assets/images/first-card.jpg);
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
        border-radius: 10px;

        @media (min-width: 640px){
            width: 384px;
            height: 240px;
            margin-top: $spacing-mobile * 2;
        }

        @media (min-width: 1024px){
            margin: 0;
        }

        @media (min-width: 1440px){
            width: 432px;
            height: 270px;
        }

        &.second{
            background-image: url(../../assets/images/second-card.jpg);
        }

        &-info{
            text-align: center;
            padding-top: 20px;
            font-family: $form-font;
            font-size: 1.6rem;
            font-weight: bold;
            color: white;

            @media (min-width: 1440px){
                font-size: 2rem;
            }
        }

        &-balance{
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            font-size: 2.5rem;
            color: white;
            font-family: $form-font;

            @media (min-width: 1440px){
                font-size: 3.2rem;
            }

            small{
                font-family: $menu-font;
                font-size: 1.4rem;

                @media (min-width: 1440px){
                    font-size: 1.8rem;
                }
            }
        }

        &-buttons{
            position: absolute;
            bottom: 10px;
            display: flex;
            justify-content: space-evenly;
            width: 100%;

            button{
                min-width: 100px;
                font-family: $form-font;
                background-color: rgba($color: #000000, $alpha: .5);
                color: white;
                border: 1px solid white;
                padding: 5px 10px;
                cursor: pointer;

                @media (min-width: 1440px){
                    font-size: 1.5rem;
                }
            }
        }
    }
}

.history{
    @include walletSpacing;

    &__title{
        @include walletTitle;
    }
}

.settlement{
    @include walletSpacing;
    padding-bottom: 50px;

    &__title{
        @include walletTitle;
    }

    &__number{
        margin-top: $spacing-desktop;
        font-size: 1.3rem;

        @media (min-width: 640px){
            font-size: 1.5rem;
        }

        @media (min-width: 1024px){
            font-size: 1.9rem;
        }

        strong{
            display: inline-block;
            padding-bottom: 5px;
        }

        button{
            @include walletBtn
        }
    }

    &__invoices{
        &-title{
            margin-top: $spacing-mobile;
            font-size: 1.3rem;
            font-weight: bold;

            @media (min-width: 640px){
                font-size: 1.5rem;
            }
    
            @media (min-width: 1024px){
                font-size: 1.9rem;
            }
        }

        &-text{
            padding-top: 5px;

            @media (min-width: 640px){
                font-size: 1.5rem;
            }
    
            @media (min-width: 1024px){
                font-size: 1.9rem;
            }
        }

        button{
          @include walletBtn
        }
    }
}