@import '../variables';
@import '../mixins';

.notifications{
    width: 90%;
    margin: auto;
    
    @media (min-width: 640px){
        width: 80%;
    }

    @media (min-width: 1024px){
        width: auto;
        margin-left: 30%;
        margin-top: $spacing-mobile;
    }

    @media (min-width: 1440px){
        margin-left: 25%;
    }

    &__title{
        @include settingsTitle;
    }
}