@import 'variables';

@mixin fontGradientColor{
    background: #0022FF;
    background: linear-gradient(to right, #0022FF 0%, #99B4FF 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

@mixin registerformDesktop($mt, $fz){
    margin-left: 50px;
    color: white;
    margin-top: $mt;
    font-size: $fz;
    font-family: $header-font;
}

@mixin loginregisterContainer{
    background-image: url(../assets/images/bg_register.jpg);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    padding-bottom: $spacing-mobile;
    min-height: 100vh;

    @media (min-width: 1024px){
        display: flex;
        padding-bottom: 0;
    }
}

@mixin loginregisterDesktop{
    display: none;

    @media (min-width: 1024px){
        flex-basis: 40%;
        height: inherit;
        display: block;
        background-image: url(../assets/images/bg_register.jpg);
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
    }
}

@mixin loginregisterForm{
    width: 90%;
    margin-left: 5%;
    padding: 0 5% $spacing-desktop 5%;
    border: 1px solid black;
    background-color: #fff;
    border-radius: 20px;

    @media (min-width: 640px){
        width: 70%;
        margin-left: 15%;
    }

    @media (min-width: 1024px){
        display: flex;
        margin-left: 0;
        flex-basis: 60%;
        border: none;
        border-radius: 0;
        align-items: center;
        padding-left: 15%;
        padding-right: 15%;
    }
}

@mixin loginregisterTitle{
    text-align: center;
    padding-top: $spacing-mobile;
    font-size: 2.2rem;
    margin-bottom: $spacing-mobile;

    @media (min-width: 1024px){
        font-size: 3rem;
    }
}

@mixin walletTitle{
    font-size: 1.7rem;
    color: #444;

    @media (min-width: 1024px){
        font-size: 2.2rem;
    }
}

@mixin walletSpacing{
    width: 90%;
    margin: auto;
    margin-top: $spacing-mobile * 2;

    @media (min-width: 1024px){
        margin-left: 30%;
        margin-right: 5%;
        width: auto;
    }

    @media (min-width: 1440px){
        margin-right: 10%;
    }
}

@mixin walletBtn{
    margin-top: 10px;
    text-transform: uppercase;
    border-radius: 10px;
    background-color: white;
    border: 2px solid $special-fontcolor;
    padding: 5px 10px;
    font-family: $form-font;
    cursor: pointer;

    &:hover{
        background-color: $special-fontcolor;
        border-color: white;
    }
}

@mixin settingsTitle{
    color: #444;
    font-size: 1.8rem;
    text-decoration: underline;

    @media (min-width: 1024px){
        font-size: 2.2rem;
    }
}