@import '../variables';

.panel__container{
    min-height: 100vh;
    width: 100%;
    background-color: #eee;

    .mobile{
        display: flex;
        justify-content: space-between;
        padding: $spacing-mobile;
        background-color: lighten($color: #0464FC, $amount: 10%);
        border-bottom: 1px solid #999;
        color: white;

        @media (min-width: 1024px){
            display: none;
        }

        a{
            color: white;
        }

        &__text{
            font-size: 2rem;
        }

        &__icon{
            font-size: 2rem;
        }

        &__settings{
            position: fixed;
            top: 0;
            left: 0;
            right: 0;
            height: 65vh;
            background-color: #0464FC;
            z-index: 1;

            @media (min-width: 1024px){
                display: none;
            }

            &-icon{
                position: fixed;
                color: white;
                font-size: 2rem;
                top: 15px;
                right: 15px;
            }

            &-list{
                display: flex;
                flex-direction: column;
                height: 100%;
                align-items: center;

                .mobile__settings-item{
                    flex-basis: calc(100% / 7);
                    display: flex;
                    padding-left: 10%;
                    align-items: center;
                    width: 100%;
                    border-bottom: 1px solid #ccc;
                    color: white;
                    font-size: 1.5rem;

                    svg{
                        margin-right: 5px;
                        width: 25px;
                    }

                    a{
                        padding-left: 5px;
                        text-decoration: none;
                        color: white;
                        font-size: 1.4rem;
                    }
                }
            }
        }
    }

    .desktop{
        display: none;

        @media (min-width: 1024px){
            color: white;
            display: block;
            position: fixed;
            top: 0;
            bottom: 0;
            background-color: lighten($color: #0464FC, $amount: 10%);
            border-right: 1px solid #999;
            width: 25%;
        }

        @media (min-width: 1440px){
            width: 20%;
        }

        &__back{
            display: flex;
            margin-left: 10%;
            margin-top: $spacing-mobile;
            font-size: 1.6rem;
            color: white;
            text-decoration: none;
            cursor: pointer;

            svg{
                margin-right: 10px;
            }
        }

        &__list{
            margin-top: 25%;
            margin-left: 10%;

            .desktop__item{
                font-size: 1.8rem;
                margin-top: $spacing-desktop * 2;
                color: white;
                cursor: pointer;

                &.active{
                    border-right: 4px solid white;
                }

                &:hover{
                    text-decoration: underline;
                }

                svg{
                    margin-right: 5px;
                    width: 25px;
                }
            }
        }
    }

    .select{
        position: relative;
        width: 80%;
        margin: $spacing-mobile auto;

        @media (min-width: 1024px){
            margin: 0 0 0 25%;
            width: auto;
        }

        @media (min-width: 1440px){
            margin: 0 0 0 20%;
            width: auto;
        }

        &__title{
            text-align: center;
            font-size: 2rem;
            color: #222;
            font-family: $form-font;
            width: 50%;
            margin: auto;
            padding: 5px 0;
            border: 2px solid gray;
            border-radius: 5px;
            background-color: white;

            @media (min-width: 1024px){
                width: auto;
                margin: 0;
                padding: $spacing-desktop;
                border: none;
                text-align: left;
                border-bottom: 2px solid gray;
                border-radius: 0;
            }

            &::before, &::after{
                position: absolute;
                top: 50%;
                display: inline-block;
                content: "";
                width: 25%;
                height: 2px;
                background-color: gray;

                @media (min-width: 1024px){
                    display: none;
                }
            }

            &::before{
                left: 0;
            }

            &::after{
                right: 0;
            }
        }
    }
}